import React from 'react';
import { IPrecioProps } from '../interfaces';

const Precio: React.FunctionComponent<IPrecioProps> = (props) => {
  const {
    price,
    percent_change_1h,
    percent_change_7d,
    percent_change_24h
  } = props.precioBitcoin;
  return (
    <div className="card text-white bg-success mb-3">
      <div className="card-header">Precio del Bitcoin</div>
      <div className="card-body">
        <h4 className="card-title">Precio Actual: $ {price}</h4>
        <div className="d-md-flex justify-content-between">
          <p className="card-text">
            <span className="font-weight-bold">Última hora: </span>
            {percent_change_1h} %
          </p>
          <p className="card-text">
            <span className="font-weight-bold">
              Últimas 24 horas: {percent_change_24h} %
            </span>
          </p>
          <p className="card-text">
            <span className="font-weight-bold">
              Últimos 7 días: {percent_change_7d} %
            </span>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Precio;
